import config from 'react-global-configuration';
import axios from './axios-interceptor';

const storeUserQuestion = (userId,questionId,optionAnswerId,quizLink)=>{
  return  axios.post(`${config.get('apiUrl')}/user-test`,{userId,questionId,optionAnswerId,quizLink},   );
}

const getUserQuizById=  (link)=>{
   return  axios.get(`${config.get('apiUrl')}/user-test/${link}`,   );
}

const storeResponses= (userId,questionId,optionAnswerId,quizLink) =>{
    return axios.post(`${config.get('apiUrl')}/other-test`,{userId,questionId,optionAnswerId,quizLink},  );
}
const getQuestionsByCategory = (categoryId)=>{
    return axios.get(`${config.get('apiUrl')}/topic-question/${categoryId}`, );
}
const getLoggedInUserQuiz = () => {
    return axios.get(`${config.get('apiUrl')}/get-quizzes`,  );
}
const getQuestionsAndAnswers = (quizLink) =>{
    return axios.get(`${config.get('apiUrl')}/get-quizzes/${quizLink}`, );
}

const getCategoryById = (id)=>{
  return axios.get(`${config.get('apiUrl')}/category/${id}`, );
}
const getResultDetails = (data)=>{
  return axios.get(`${config.get('apiUrl')}/result-details?quizLink=${data.quizLink}&userId=${data.name}`, );
}
export default {
    storeUserQuestion,
    getUserQuizById,
    storeResponses,
    getQuestionsByCategory,
    getLoggedInUserQuiz,
    getQuestionsAndAnswers,
    getCategoryById,
    getResultDetails
}
