import React, { Component } from 'react';
import Created from './new/Created'; 
import { generateLinkString } from '../../../../_helpers/link';
import Quiz from './new/Quiz';
import { Center } from '../../../view-utils/Framework';
import ShareableLink from '../../quiz/links/ShareableLink';
import service from '../../../../_services/user-question.service';
import './index.css';
 

class QuestionContainer extends Component {

  constructor(props) {
    super(props)
    const quizId = this.props.match.params.id;
    const userId = this.props.match.params.username;
    this.state = {
      title: '',
      questions: [],
      userId: userId,
      quizId: quizId,
      link: this.generateLink(),
      questionTag: 1,
      counter: 0,
      questionId: 1,
      question: '',
      answerOptions: [],
      answer: '',
      answersCount: {},
      isFinished: false

    }
    this.handleAnswerSelected = this.handleAnswerSelected.bind(this);
  };

  generateLink() {
    var generatedLink = generateLinkString(10);
    return generatedLink;

  }


  async componentDidMount() {
    var self = this;
    await service.getQuestionsByCategory(self.state.quizId).then(response => {
      console.log(response);

      self.setState({
        title: response.data.title,
        questions: response.data.questions,
        quizId: self.state.quizId,
        userId: self.state.userId,

      })
    }).catch(function (error) {
      console.log(error);
    });


    const shuffledAnswerOptions = this.state.questions.map(question =>
      this.shuffleArray(question.options)
    );
    this.setState({
      question: this.state.questions[0].question_text,
      answerOptions: shuffledAnswerOptions[0],
      questionTag: this.state.questions[0].id
    });
  }
  shuffleArray(array) {
    var currentIndex = array.length,
      temporaryValue,
      randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;

      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }

    return array;
  }
  async setUserAnswer(answer) {
   let currentQuestion = this.state.questions[this.state.questionId-1];
   console.log(currentQuestion);
   
   await service.storeUserQuestion(this.state.userId, currentQuestion.id, answer, this.state.link).then(response => {
      console.log(response);
    }).catch(err => {
      console.log(err);
    });

  }

  async handleAnswerSelected(event) {

   await this.setUserAnswer(event.currentTarget.value);
    
    if (this.state.questionId < this.state.questions.length) {
      setTimeout(() => this.setNextQuestion(), 300);
    } else {

      setTimeout(() => this.setIsFinished(this.getIsFinished()), 300);


    }
  }

  setIsFinished(isFinished) {
    this.setState({
      isFinished: !isFinished
    })
  }
  getIsFinished() {
    return this.state.isFinished;
  }
  setNextQuestion() {
    const counter = this.state.counter + 1;
    const questionId = this.state.questionId + 1;

    this.setState({
      counter: counter,
      questionId: questionId,
      question: this.state.questions[counter].question_text,
      answerOptions: this.state.questions[counter].options,
      questionTag: this.state.questions[0].id,
      answer: ''
    });
  }




  renderQuiz() {
    return (
      <Center>
        <Quiz
          answer={this.state.answer}
          answerOptions={this.state.answerOptions}
          questionId={this.state.questionId}
          question={this.state.question}
          questionTotal={this.state.questions.length}
          onAnswerSelected={this.handleAnswerSelected}
        />
      </Center>
    );
  }
  renderCreated() {

    return (
      <div>
        <Center>
          <Created username={this.state.userId} />

          <ShareableLink link={`${this.state.link}`}
          />
        </Center>
      </div>
    )
  }
  render() {
 

    return (
      <div className='question-container'>
        {

          this.state.isFinished === true ? this.renderCreated() : this.renderQuiz()

        }


      </div>
    );
  }
};

export default QuestionContainer;
